<template>
  <div>
    <el-header class="header_bg">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>{{(asideNavTitle[0] === '' || asideNavTitle[0] === undefined )? '':asideNavTitle[0]}}
          </el-breadcrumb-item>
          <el-breadcrumb-item>{{(asideNavTitle[1] === '' || asideNavTitle[1] === undefined )? '':asideNavTitle[1]}}
          </el-breadcrumb-item>
        </el-breadcrumb>
      </el-breadcrumb>
    </el-header>
    <div class="align_right">
      <el-button @click="addPersonnel()" class="addBtn" size="small"></el-button>
    </div>
    <el-table
      :data="tableData"
      stripe
      style="width: 100%" class="marginTop10">
      <el-table-column
        prop="entDepartmentName"
        label="">
      </el-table-column>
      <el-table-column
        prop="empNumber"
        label="">
        <!--empNumber: entEmpNumber:-->
      </el-table-column>
      <el-table-column
        prop="order"
        label="">
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="200">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="modifyStaffInfo(scope.row)"></el-button>
          <el-button type="text" size="small" @click="deleteStaffInfo(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block" v-if="total>0">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="10"
        layout="total, prev, pager, next"
        :total=total>
      </el-pagination>
    </div>

    <!---->
    <el-dialog title="" :visible.sync="addDepartFormVisible" width="40%" center>
      <el-form :model="addDepartForm" :rules="rules" ref="addDepartForm">
        <el-form-item label="" :label-width="formLabelWidth" prop="departmentName">
          <el-input v-model="addDepartForm.departmentName" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="departmentNumber">
          <el-input type="number" v-model.number="addDepartForm.departmentNumber" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDepartFormVisible = false"> </el-button>
        <el-button type="primary" @click="sureAddDepartment()"> </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="modifyDepartFormVisible" width="40%" center>
      <el-form :model="departRowData" :rules="rules" ref="addDepartForm">
        <el-form-item label="" :label-width="formLabelWidth" prop="entDepartmentName">
          <el-input v-model="departRowData.entDepartmentName" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="modifyDepartFormVisible = false"> </el-button>
        <el-button type="primary" @click="sureModifyDepartment()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Verify from '../../utils/utils';
  import swal from 'sweetalert2'; //alert
  import {mapGetters} from 'vuex'
  export default {
    name: "FirmStructure",
    props: ['selectTitle'],
    data() {
      return {
        tableData: [],
        currentPage: 1,
        total: 0,
        addDepartFormVisible: false,
        modifyDepartFormVisible: false,
        addDepartForm: {
          departmentName: '',
          departmentNumber:''
        },
        departRowData: [],
        formLabelWidth: '100px',
        // selectTitle:['selectTitle'],
        rules: {
          departmentName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          departmentNumber:[
            {required: true, message: '', trigger: 'blur'},
            { type: 'number', message: ''}
          ],
          entDepartmentName:[
            {required: true, message: '', trigger: 'blur'}
          ],
        }

      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },

    created() {
      this.getDepartmentList();
    },
    methods: {
      getDepartmentList() {
        let enterpriseId = localStorage.getItem("enterpriseId");
        let departUrl = "/api/enterprise/departments/" + enterpriseId;
        this.$get(departUrl, {
          // enterpriseId: enterpriseId,
        }).then(res => {
          console.log("depart=" + JSON.stringify(res))
          if (JSON.stringify(res.data.code) == 200) {
            this.tableData = res.data.data.items;
            this.total = res.data.data.total;
            // this.currentPage = res.data.data.page;
          } else {
            swal("",res.data.msg, "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },
      addPersonnel() {
        this.addDepartForm.departmentName = '';
        this.addDepartForm.departmentNumber = '';
        this.addDepartFormVisible = true;
      },
      sureAddDepartment() {
        if (Verify.validateIsNull(this.addDepartForm.departmentName)){
          let enterpriseId = localStorage.getItem("enterpriseId");
          let addDepartUrl = "/api/enterprise/department/" + enterpriseId;
          let data = {
            ennDepartmentName: this.addDepartForm.departmentName,
          };
          if (Verify.validateIsNull(this.addDepartForm.departmentNumber)) {
            data['numberEmps'] = this.addDepartForm.departmentNumber
          }
          this.$post(addDepartUrl, data).then(res => {
            console.log(JSON.stringify(res))
            if (JSON.stringify(res.data.code) == 200) {
              this.getDepartmentList();
              this.addDepartFormVisible = false;
              // console.log("depart data==" + JSON.stringify(res.data));
            } else {
              swal("",res.data.msg, "error");
            }
          }).catch(function (reason) {
            console.log(JSON.stringify(reason));
          });
        }else {
          swal("", "", "error");
        }

      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currentPage = val;
      },
      modifyStaffInfo(row) {
        this.departRowData = row;
        this.modifyDepartFormVisible = true;
        console.log("rowData=="+JSON.stringify(row))
      },
      sureModifyDepartment() {
        if (Verify.validateIsNull(this.departRowData.entDepartmentName)){
          let departId = this.departRowData.entDepartmentId;
          let editDepartUrl = "/api/enterprise/department/" + departId;
          this.$put(editDepartUrl, {
            // departmentId: departId,
            entDepartmentName: this.departRowData.entDepartmentName,
          }).then((res) => {
            console.log("==" + JSON.stringify(JSON.stringify(res.data)))
            if (JSON.stringify(res.data.code) == 200) {
              this.getDepartmentList();
              this.modifyDepartFormVisible = false;
              swal("", "", "success");
            } else {
              swal("",res.data.msg, "error");
            }
          })
        }else{
          swal("", "", "error");
        }

      },
      deleteStaffInfo(row) {
        swal({
          title: "",
          text: "",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "",
          cancelButtonText: ""
        }).then((result) => {
          if (result.value) {
            let departId = row.entDepartmentId;
            let deleteDepartUrl = "/api/enterprise/department/" + departId;
            this.$del(deleteDepartUrl, {
              employeeId: departId
            }).then((res) => {
              if (JSON.stringify(res.data.code) == 200) {
                this.getDepartmentList();
                swal("", "", "success");
              } else {
                swal("",res.data.msg, "error");
              }
            })
          } else {
            swal("Cancelled", "", "");
          }
        });
      },
    }

  }
</script>

<style scoped>
  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .el-breadcrumb {
    font-size: 18px;
    line-height: 3.5;
    color: #333333;
  }

  .align_right {
    text-align: right;
  }

  .addBtn {
    background-color: #33B2E8;
    color: #FFFFFF;
    font-size: 17px;
  }
</style>



