<template>
  <div>
    <el-header class="header_bg">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>{{asideNavTitle === ''?"":asideNavTitle[0]}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{asideNavTitle === ''?'':asideNavTitle[1]}}</el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <el-button @click="importStaff = true" class="leadBtn" size="small" style="float: right"></el-button>
    <el-row :gutter="20" type="flex" class="search_div">
      <el-col :span="12">
        <el-row>
          <el-col :span="12">
            <el-select v-model="department" placeholder="" class="select_width">
              <el-option
                v-for="item in departments"
                :key="item.departmentId"
                :label="item.departmentName"
                :value="item.departmentId">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="12">
            <el-input v-model="staffName" placeholder="">
              <el-button slot="append" icon="el-icon-search" @click="searchEvent()"></el-button>
            </el-input>

          </el-col>
        </el-row>
      </el-col>
      <el-col :span="6" class="float_left">
        <p><span class="addStaff">{{total}}</span></p>
      </el-col>
      <el-col :span="6">
        <p class="addStaff" @click="addStaff()"><i class="el-icon-circle-plus"></i></p>
      </el-col>
    </el-row>
    <el-tabs v-model="activeName" @tab-click="tabHandleClick">
      <el-tab-pane label="" name="first" v-model="allData">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%" class="marginTop10">
          <el-table-column
            prop="employeeName"
            label="">
          </el-table-column>
          <el-table-column
            prop="phoneNumber"
            label="">
          </el-table-column>
          <el-table-column
            prop="departmentName"
            label="">
          </el-table-column>
          <el-table-column
            prop="active"
            label="">
            <template slot-scope="scope">
              {{ scope.row.active===1 ? '': ''}}
              <!--<template v-if="scope.row.status === 'pending'">-->
              <!---->
              <!--</template>-->
              <!--<template v-else-if="scope.row.status === 'passed'">-->
              <!---->
              <!--</template>-->
              <!--<template v-else-if="scope.row.status === 'rejected'">-->
              <!---->
              <!--</template>-->
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="modifyStaffInfo(scope.row)"></el-button>
              <el-button type="text" size="small" @click="deleteStaffInfo(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-if="total>0">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="total, prev, pager, next"
            :total=total>
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%" class="marginTop10">
          <el-table-column
            prop="employeeName"
            label="">
          </el-table-column>
          <el-table-column
            prop="phoneNumber"
            label="">
          </el-table-column>
          <el-table-column
            prop="departmentName"
            label="">
          </el-table-column>
          <el-table-column
            prop="active"
            label="">
            <template slot-scope="scope">
              {{ scope.row.active===1 ? '': ''}}
              <!--<template v-if="scope.row.status === 'pending'">-->
              <!---->
              <!--</template>-->
              <!--<template v-else-if="scope.row.status === 'passed'">-->
              <!---->
              <!--</template>-->
              <!--<template v-else-if="scope.row.status === 'rejected'">-->
              <!---->
              <!--</template>-->
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="modifyStaffInfo(scope.row)"></el-button>
              <el-button type="text" size="small" @click="deleteStaffInfo(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-if="total>0">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="total, prev, pager, next"
            :total=total>
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="third">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%" class="marginTop10">
          <el-table-column
            prop="employeeName"
            label="">
          </el-table-column>
          <el-table-column
            prop="phoneNumber"
            label="">
          </el-table-column>
          <el-table-column
            prop="departmentName"
            label="">
          </el-table-column>
          <el-table-column
            prop="active"
            label="">
            <template slot-scope="scope">
              {{ scope.row.active===1 ? '': ''}}
              <!--<template v-if="scope.row.status === 'pending'">-->
              <!---->
              <!--</template>-->
              <!--<template v-else-if="scope.row.status === 'passed'">-->
              <!---->
              <!--</template>-->
              <!--<template v-else-if="scope.row.status === 'rejected'">-->
              <!---->
              <!--</template>-->
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="200">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="modifyStaffInfo(scope.row)"></el-button>
              <el-button type="text" size="small" @click="deleteStaffInfo(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-if="total>0">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="total, prev, pager, next"
            :total=total>
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>

    <el-dialog
      title=""
      :visible.sync="importStaff"
      width="40%"
      :before-close="handleClose">
      <div class="import_div">
        <el-button plain class="select_file"></el-button>
        <p class="import_prompt">.xlsx (.xls.xlsx)</p>
        <el-button class="sure_import_btn"></el-button>
        <p class="float_left download_demo"><a></a></p>
      </div>

      <span slot="footer" class="dialog-footer">
    <el-button @click="importStaff = false"> </el-button>
    <el-button type="primary" @click="importStaff = false"> </el-button>
  </span>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="addStaffFormVisible" width="40%" center>
      <el-form :model="addStaffInfoForm" ref="addStaffInfoForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addStaffInfoForm.staffName" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addStaffInfoForm.phoneNumber" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="addStaffInfoForm.depart" placeholder="">
            <el-option
              v-for="item in departments"
              :key="item.departmentId"
              :label="item.departmentName"
              :value="item.departmentId">
            </el-option>
          </el-select>
          <p></p>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addStaffFormVisible = false"> </el-button>
        <el-button type="primary" @click="sureAddStaff()"> </el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="modifyStaffNameVisible" width="40%" center>
      <el-form :model="staffRowData">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="staffRowData.employeeName" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="staffRowData.departmentId" placeholder="">
            <el-option
              v-for="item in departments"
              :key="item.departmentId"
              :label="item.departmentName"
              :value="item.departmentId">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="modifyStaffNameVisible = false"> </el-button>
        <el-button type="primary" @click="sureModifyAddress()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Verify from '../../utils/utils';
  import swal from 'sweetalert2'; //alert
  import {mapGetters} from 'vuex'
  export default {
    name: "FirmMember",
    props: ['selectTitle'],
    data() {
      return {
        tableData: [],
        activeName: 'first',
        status: '',
        allData: '',
        currentPage: 1,
        total: 0,
        department: '',
        departments: [],
        staffName: '',
        importStaff: false,
        addStaffFormVisible: false,
        modifyStaffNameVisible: false,
        formLabelWidth: '100px',
        addStaffInfoForm: {
          staffName: '',
          phoneNumber: '',
          depart: '',
        },
        staffRowData: [],
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },
    created() {
      this.getDepartmentList();
      this.getFirmMemberList('1');
      console.log('222===',this.asideNavTitle);
    },
    methods: {
      searchEvent() {
        this.searchFirmMemberList();
      },
      getDepartmentList() {
        let enterpriseId = localStorage.getItem("enterpriseId");
        let departUrl = "/api/enterprise/departments/" + enterpriseId + "/search";
        this.$get(departUrl, {
          enterpriseId: enterpriseId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.departments = res.data.data.items;
          } else {
            swal("",res.data.msg, "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },
      searchFirmMemberList(){
        if (this.activeName === "first") {
          this.getFirmMemberList('1');
        }else if (this.activeName === "second"){
          this.getFirmMemberList('0');
        }else{
          this.getFirmMemberList('2');
        }
        // let enterpriseId = localStorage.getItem("enterpriseId");
        // let firmMemberUrl = "/api/enterprise/employees/" + enterpriseId;
        // this.$get(firmMemberUrl, {
        //   // enterpriseId: enterpriseId,
        //   page: this.currentPage,
        //   perPage: 10,
        //   optName: this.staffName,
        //   departmentId: this.department
        //   // departmentId: ''
        // }).then(res => {
        //   console.log(JSON.stringify(res))
        //   if (JSON.stringify(res.data.code) == 200) {
        //     this.tableData = res.data.data.items;
        //     this.total = res.data.data.total;
        //   } else {
        //     swal("",res.data.msg, "error");
        //   }
        //   // console.log("res=="+JSON.stringify(res))
        // }).catch(function (reason) {
        //   console.log(JSON.stringify(reason));
        // });
      },
      getFirmMemberList(str) {
        let enterpriseId = localStorage.getItem("enterpriseId");
        let firmMemberUrl = "/api/enterprise/employees/" + enterpriseId;
        let data = {
          status: str
        };
        if (Verify.validateIsNull(this.department)) {
          data['departmentId'] = this.department;
        }
        if (Verify.validateIsNull(this.staffName)) {
          data['optName'] = this.staffName;
        }
        this.$get(firmMemberUrl, data).then(res => {
          console.log(JSON.stringify(res))
          if (JSON.stringify(res.data.code) == 200) {
            this.tableData = res.data.data.items;
            this.total = res.data.data.total;
            // this.currentPage = res.data.data.page;
            console.log("data ==" + JSON.stringify(res));
          } else {
            swal("",res.data.msg, "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },
      addStaff() {
        this.addStaffFormVisible = true;
      },
      sureAddStaff() {
        if (Verify.validateIsNull(this.addStaffInfoForm.staffName) &&
          Verify.validateIsNull(this.addStaffInfoForm.depart) &&
          Verify.phoneNumberVerify(this.addStaffInfoForm.phoneNumber))
        {
          let enterpriseId = localStorage.getItem("enterpriseId");
          let addStaffUrl = "/api/enterprise/employee/" + enterpriseId;
          this.$post(addStaffUrl, {
            enterpriseId: enterpriseId,
            name: this.addStaffInfoForm.staffName,
            phoneNumber: this.addStaffInfoForm.phoneNumber,
            departmentId: this.addStaffInfoForm.depart,
          }).then(res => {
            console.log(JSON.stringify(res))
            if (JSON.stringify(res.data.code) == 200) {
              this.addStaffFormVisible = false;
              this.getFirmMemberList();
              console.log("data==" + JSON.stringify(res.data));
            } else {
              swal("",res.data.msg, "error");
            }
            // console.log("res=="+JSON.stringify(res))
          }).catch(function (reason) {
            console.log(JSON.stringify(reason));
          });
        }else{
          alert("");
        }
      },
      modifyStaffInfo(row) {
        this.staffRowData = row;
        this.modifyStaffNameVisible = true;
        console.log("staffRowData="+JSON.stringify(row))
      },
      sureModifyAddress() {
        let staffId = this.staffRowData.entEmployeeId;
        let deleteAddressUrl = "/api/enterprise/employee/" + staffId;
        this.$put(deleteAddressUrl, {
          // employeeId: staffId,
          name: this.staffRowData.employeeName,
          departmentId:this.staffRowData.departmentId
        }).then((res) => {
          console.log("==" + JSON.stringify(JSON.stringify(res.data)))
          if (JSON.stringify(res.data.code) == 200) {
            this.modifyStaffNameVisible = false;
            swal("", "", "success");
          } else {
            swal("",res.data.msg, "error");
          }
          this.getFirmMemberList();
        })
      },
      deleteStaffInfo(row) {
        swal({
          title: "",
          text: "",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "",
          cancelButtonText: ""
        }).then((result) => {
          if (result.value) {
            let staffId = row.employeeAccountId;
            let deleteEmployUrl = "/api/enterprise/employee/" + staffId;
            this.$del(deleteEmployUrl, {
              // employeeId: staffId
            }).then((res) => {
              if (JSON.stringify(res.data.code) == 200) {
                if (this.activeName === "first") {
                  this.getFirmMemberList('1');
                }else if (this.activeName === "second"){
                  this.getFirmMemberList('0');
                }else{
                  this.getFirmMemberList('2');
                }
                swal("", "", "success");
              } else {
                swal("",res.data.msg, "error");
              }
            })
          } else {
            swal("Cancelled", "", "");
          }
        });
      },
      tabHandleClick(tab, event) {
        console.log(tab, event);
        if (tab.name === "first") {
          this.getFirmMemberList('1');
        }else if (tab.name === "second"){
          this.getFirmMemberList('0');
        }else{
          this.getFirmMemberList('2');
        }
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currentPage = val;
      },
      handleClose(done) {
        // this.$confirm('')
        //   .then(_ => {
        done();
        //   })
        //   .catch(_ => {});
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/personal/personal-center.scss";

  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .el-breadcrumb {
    font-size: 18px;
    line-height: 3.5;
    color: #333333;
  }

  .align_right {
    text-align: right;
  }

  .leadBtn {
    float: right;
    margin-top: -48px;
    margin-right: 10px;
  }

  .search_div {
    margin-top: 10px;
  }

  .addStaff {
    color: #009BDD;
    font-size: 16px;
  }

  .import_div {
    margin: 0 auto;
    width: 260px;
  }

  .select_file {
    width: 200px;
    color: #009BDD;
    border-color: #009BDD;
  }

  .import_prompt {
    color: #666666;
    font-size: 14px;
  }

  .sure_import_btn {
    width: 260px;
    color: #FFFFFF;
    background-color: #009BDD;
    font-size: 17px;
  }

  .download_demo {
    color: #009BDD;
    font-size: 14px;
  }
</style>




