<template>
  <div class="padding_left_right">
    <el-header>
      <el-row>
        <el-col :span="10">
          <div class="logo_img_div">
            <img src="../../assets/logo_login@2x.png" class="logo_img"/>
          </div>
        </el-col>
        <el-col :span="12" class="margin_top15">
          <el-steps :active="active" align-center>
            <el-step title="" description=""></el-step>
            <el-step title="" description=""></el-step>
            <el-step title="" description=""></el-step>
            <el-step title="" description=""></el-step>
          </el-steps>
          <!--<el-button style="margin-top: 12px;" @click="next()"></el-button>-->
        </el-col>
      </el-row>
    </el-header>

    <el-row class="margin_top18">
      <el-col :span="12" class="text_align_left">
        <p>{{orderNumber}}</p>
      </el-col>
      <el-col :span="12">
        <p class="info_title">:<span class="payable">{{amountPayable}}</span></p>
      </el-col>
    </el-row>
    <hr>

    <div v-if="paymentMethod === 'TransferPayment'">
      <p class="payTitle"><span class="info_content">(3)</span></p>
      <ol>
        <li class="info_content text_align_left">
          123222222
          
        </li>
        <li class="info_content text_align_left">
          
        </li>
        <li class="info_content text_align_left">
          7103
        </li>
      </ol>
      <p class="payTitle"></p>
      <div class="text_align_left padding_left20">
        <p class="info_title"><span class="info_content">{{accountName}}</span></p>
        <p class="info_title"><span class="info_content">{{account}}</span></p>
        <p class="info_title"><span class="info_content">{{bank}}</span></p>
        <p class="info_title"><span class="info_content">{{bankNumber}}</span></p>
        <p class="info_title"><span class="info_content">{{orderNumber}}</span></p>
      </div>
    </div>
    <div v-else>
      <p class="payTitle"></p>
      <div class="margin_left80">
        <p class="info_content text_align_left"><span class="info_content">{{second}}</span>
        </p>
        <div style="width: 160px;height: 160px;background-color:#e6e6e6"></div>
      </div>
    </div>

  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: "OrderPayment",
    data() {
      return {
        active:2,
        second: 58,
        amountPayable: '1500.00',
        orderNumber: '12322222',
        accountName: 'XXX',
        account: '2222 3333 4444 123',
        bank: '',
        bankNumber: '2222 3333 4444',//
      }
    },
    computed: {
      ...mapGetters(['paymentMethod','cartParams'])
    },
    methods: {}
  }
</script>

<style scoped>
  @import "../../styles/personal/personal-center.scss";

  .logo_img_div {
    /*margin-left: 6.75rem;*/
    text-align: left;
  }

  .logo_img {
    text-align: left;
    height: 60px;
    margin-top: 0.75rem;
  }
  .margin_top18 {
    margin-top: 1.6%;
  }
  .margin_left80 {
    margin-left: 10%;
  }

  .commit_info {

  }

  .payTitle {
    font-size: 18px;
    font-weight: bold;
    color: #333333;
    padding-top: 10px;
    padding-left: 10px;
    text-align: left;
  }

  .info_title {
    font-size: 14px;
    color: #404040;
    font-weight: bold;
  }

  .info_content {
    font-size: 14px;
    color: #666666;
    font-weight: normal;
  }

  .payable {
    font-size: 24px;
    color: #DF2321;
  }
</style>



