<template>
  <div class="ball-container">
    <transition-group tag="div" @before-enter='beforeEnter' @enter='enter' @after-enter='afterEnter'>
      <div class="ball" v-for="(item, index) in balls" :key=item.index v-show="item.show" transition='drop'>
        <div class="inner inner-hook"></div>
        </div>
    </transition-group>
  </div>
</template>

<script>
  export default {
    name: "ShopCartBalls",
    data() {
      return {
        balls: [
          {show:false,index:0},
          {show:false,index:1},
          {show:false,index:2},
          {show:false,index:3},
          {show:false,index:4},
          {show:false,index:5},
        ],
        dropBalls: []
      }
    },
    methods: {
      drop(el) {
        //
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i];
          if (!ball.show) { //falsedropBalls
            ball.show = true;
            ball.el = el; //eldom
            this.dropBalls.push(ball);
            return;
          }
        }
      },
      beforeEnter(el){ //vue
        let count = this.balls.length;
        while (count--) {
          let ball = this.balls[count];
          if (ball.show) {
            let rect = ball.el.getBoundingClientRect(); //()
            let x = -rect.left -130;
            // let y = -(window.innerHeight - rect.top - 22); //,
            // let x = rect.right - 100;
            let y = window.innerHeight - rect.top +330;
            el.style.display = ''; //display
            el.style.webkitTransform = `translate3d(0,${y}px,0)`;
            el.style.transform = `translate3d(0,${y}px,0)`;
            debugger
            //
            let inner = el.getElementsByClassName('inner-hook')[0]; //inner-hookjs
            inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
            inner.style.transform = `translate3d(${x}px,0,0)`;
          }
        }
      },

      enter(el, done) { //vue
        /* eslint-disable no-unused-vars */
        let rf = el.offsetHeight; //html
        this.$nextTick(() => { //,
          el.style.webkitTransform = 'translate3d(0,0,0)';
          el.style.transform = 'translate3d(0,0,0)';
          //
          let inner = el.getElementsByClassName('inner-hook')[0]; //inner-hookjs
          inner.style.webkitTransform = 'translate3d(0,0,0)';
          inner.style.transform = 'translate3d(0,0,0)';
          el.addEventListener('transitionend', done); //Vue
        });
      },

      afterEnter(el) { //vue
        let ball = this.dropBalls.shift(); //dropBalls
        if (ball) {
          ball.show = false;
          // el.style.display = 'none'; //
        }
      }
    }
  }
</script>

<style scoped>
  .ball-container,
  .ball {
    position:fixed;
    left: 32px;
    bottom: 22px;
    z-index: 200;
    transition: all .6s cubic-bezier(0.49, -0.29, 0.75, 0.41)
  }
  .inner {
    width:16px ;
    height:16px;
    border-radius: 50% ;
    background:rgb(0, 160, 220);
    transition: all .6s linear
  }
  .show{
    opacity: 0;
  }

</style>



