<template>
  <div>
    <div>
      <el-header class="header_bg">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item>{{(asideNavTitle[0] === '' || asideNavTitle[0] === undefined )?
              '':asideNavTitle[0]}}
            </el-breadcrumb-item>
            <el-breadcrumb-item>{{(asideNavTitle[1] === '' || asideNavTitle[1] === undefined )?
              '':asideNavTitle[1]}}
            </el-breadcrumb-item>
          </el-breadcrumb>
        </el-breadcrumb>
      </el-header>
      <!--:class="(modifyClass=!whetherEmail)?(!whetherTel):!(whetherPwd)-->
      <div class="margin_top15 white_bg_color padding_left20" :class="{modifyClass:whether}">
        <div class="text_align_left">
          <el-row>
            <el-col :span="3">
              <p class="left_title"></p>
            </el-col>
            <el-col :span="9">
              <p class="right_list_font">
                {{loginAccount}}
              </p>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="3">
              <p class="left_title">ID</p>
            </el-col>
            <el-col :span="9">
              <p class="right_list_font">
                {{accountID}}
              </p>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="3">
              <p class="left_title"></p>
            </el-col>
            <el-col :span="9">
              <p class="right_list_font">
                {{registerTime}}
              </p>
            </el-col>
          </el-row>
          <hr class="margin_right20">
          <el-row>
            <el-col :span="3">
              <p class="left_title"></p>
            </el-col>
            <el-col :span="9">
              <p class="right_list_font">
                6
              </p>
            </el-col>
            <el-col :span="4" class="text_align_center">
              <p v-if="pwdStatus === true">
                <img src="../../assets/firm/sccg_icon_login@2x.png" height="16" width="16"/>
                <span></span>
              </p>
              <p v-else="pwdStatus === false">
                <img src="../../assets/firm/weishezhi_icon@2x.png" height="16" width="16"/>
                <span></span>
              </p>
            </el-col>
            <el-col :span="8">
              <el-button class="modify_btn" @click="modifyBindPwd()" size="small"></el-button>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="3">
              <p class="left_title"></p>
            </el-col>
            <el-col :span="9">
              <p class="right_list_font">{{safeTel}}<span class="left_title">[]</span></p>
            </el-col>
            <el-col :span="4" class="text_align_center">
              <p v-if="safeTelStatus === true">
                <img src="../../assets/firm/sccg_icon_login@2x.png" height="16" width="16"/>
                <span></span>
              </p>
              <p v-else="safeTelStatus === false">
                <img src="../../assets/firm/weishezhi_icon@2x.png" height="16" width="16"/>
                <span></span>
              </p>
            </el-col>
            <el-col :span="8">
              <el-button class="modify_btn" @click="modifyBindTel()" size="small"></el-button>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="3">
              <p class="left_title"></p>
            </el-col>
            <el-col :span="9">
              <!--<p class="right_list_font">{{safeEmail.length !==0?safeEmail:''}}<span class="left_title">[]</span></p>-->
            </el-col>
            <el-col :span="4" class="text_align_center">
              <p v-if="safeEmailStatus === true">
                <img src="../../assets/firm/sccg_icon_login@2x.png" height="16" width="16"/>
                <span></span>
              </p>
              <p v-else="safeEmailStatus === false">
                <img src="../../assets/firm/weishezhi_icon@2x.png" height="16" width="16"/>
                <span></span>
              </p>
            </el-col>
            <el-col :span="8">
              <el-button class="modify_btn" @click="modifyBindEmail()" size="small"></el-button>
            </el-col>
          </el-row>
        </div>
      </div>
      <!---->
      <el-row class="white_bg_color bind_email_div display_none" :class="{modify:isSelectedModifyEmail}">
        <el-col :span="12">
          <el-form :model="emailForm" ref="emailForm" label-width="100px">
            <el-form-item label="" prop="bindState">
              <div v-model="emailForm.bindState" class="text_align_left unbind">{{this.emailForm.bindState===true?safeEmail:''}}</div>
            </el-form-item>
            <el-form-item
              prop="email"
              label="" :rules="rules">
              <el-input v-model="emailForm.email" placeholder=""></el-input>
              <p class="verify_hint"><span class="verify_star">*</span></p>
              <el-button class="confirm_btn" @click="confirmBindEmail()"></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12"></el-col>
      </el-row>
      <!---->
      <div class="white_bg_color bind_email_div display_none" :class="{modify:isSelectedModifyTel}">
        <el-form :model="ruleForm" status-icon :rules="telRules" ref="ruleForm"
                 class="input_width">
          <el-form-item label="" prop="telNumber" label-width="120px">
            <div v-model="ruleForm.telNumber" class="text_align_left unbind">{{ruleForm.telNumber}}</div>
          </el-form-item>
          <el-form-item label="" prop="phoneNumber" label-width="90px">
            <el-input type="tel" v-model="ruleForm.phoneNumber"
                      placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="graphCode" label-width="90px">
            <el-row :gutter="10">
              <el-col :span="10">
                <el-input type="text" v-model="ruleForm.graphCode" placeholder=""></el-input>
              </el-col>
              <el-col :span="8">
                <el-button class="img_code" @click="getImgCode()" v-show="isShow"></el-button>
                <div v-html="svg" v-show="!isShow" class="img_code_height">
                  {{svg}}
                </div>
              </el-col>
              <el-col :span="4">
                <span class="refresh_img_code" v-show="!isShow" @click="refreshImgCode()"></span>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="" prop="messageCode" label-width="90px">
            <el-row :gutter="10" style="">
              <el-col :span="12">
                <el-input v-model="ruleForm.messageCode" placeholder=""></el-input>
              </el-col>
              <el-col :span="8">
                <el-button @click="getMessageCode()"></el-button>
              </el-col>
              <el-col :span="4">

              </el-col>
            </el-row>

          </el-form-item>
          <el-form-item>
            <el-button class="login_btn" type="primary" @click="confirmModifyNewTel()"></el-button>
          </el-form-item>

        </el-form>
      </div>
      <!---->
      <div class="white_bg_color bind_email_div display_none" :class="{modify:isSelectedModifyPwd}">
        <el-form :model="resetPwdForm" status-icon :rules="resetPwdRules" ref="resetPwdForm" label-width="80px">
          <el-form-item
            prop="oldPwd"
            label="" class="width360 padding_top10">
            <el-input v-model="resetPwdForm.oldPwd" placeholder="" autocomplete="off" type="password"></el-input>

          </el-form-item>
          <el-form-item
            prop="newPwd"
            label="" label-width="80px" class="width360">
            <el-input v-model="resetPwdForm.newPwd" placeholder="620" autocomplete="off" type="password">
            </el-input>
            <el-row :gutter="4" class="width305">
              <el-col :span="5">
                <div :class="[isRed?pwdSafeRedClass:pwdComplex,pwdSafeVerifySize]"></div>
              </el-col>
              <el-col :span="5">
                <div :class="[isOrange?pwdSafeOrangeClass:pwdComplex,pwdSafeVerifySize]"></div>
              </el-col>
              <el-col :span="5">
                <div :class="[isGreen?pwdSafeGreenClass:pwdComplex,pwdSafeVerifySize]"></div>
              </el-col>
              <el-col :span="9">
                <div class="pwd_safe_prompt">
                  
                  <span v-model="pwdGrade">{{pwdGrade}}</span>
                </div>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item
            prop="confirmPwd"
            label="" class="width360">
            <el-input v-model="resetPwdForm.confirmPwd" placeholder="" autocomplete="off"
                      type="password"></el-input>

          </el-form-item>
          <el-form-item
            prop=""
            label="" class="width360">
            <el-button type="text" class="nextStep btn_color" @click="confirmModifyPwd()"></el-button>
          </el-form-item>
        </el-form>
        <div class="pwd_specification">
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Verify from '../../utils/utils';
  import swal from 'sweetalert2'; //alert
  import {mapGetters} from 'vuex'
  // import FirmModifyBindTel from "../../components/firm/FirmModifyBindTel"
  export default {
    name: "FirmSafeSetting",
    props: ['selectTitle'],

    data() {
      var validatePwd2 = (rule, value, callback) => {
        if (this.resetPwdForm.newPwd !== '') {
          if (value === '') {
            callback(new Error(''));
          } else if (value !== this.resetPwdForm.newPwd) {
            callback(new Error('!'));
          } else {
            callback();
          }
        } else {
          callback(new Error(''));
        }
      };
      return {
        safeSetting: '>',
        whether:false,
        whetherEmail: false,//
        whetherTel: false,//
        whetherPwd: false,//
        isSelectedModifyEmail:false,
        isSelectedModifyTel:false,
        isSelectedModifyPwd:false,
        loginAccount: '',//
        accountID: '',//id
        registerTime: '',//
        safeTel: '',
        safeEmail:'',
        pwdStatus:false,//
        safeTelStatus:false,
        safeEmailStatus:false,//
        emailForm: {
          bindState: false,
          email: ''
        },
        rules: [
          {required: true, message: '', trigger: 'blur'},
          {type: 'email', message: '', trigger: ['blur', 'change']}
        ],
        svg: '',
        showImgCode: '',
        isShow: false,
        ruleForm: {
          telNumber: '',
          phoneNumber: '',
          graphCode: '',
          messageCode: '',
        },
        telRules: {
          phoneNumber: [
            {validator: Verify.phoneNumberVerify, trigger: 'blur'}
          ],
          //
          graphCode: [
            {validator: Verify.graphCodeVerify, trigger: 'blur'}
          ],
          //
          messageCode: [
            {type: 'number', validator: Verify.messageCodeVerify, trigger: 'blur'}
          ],
        },
        resetPwdForm: {
          oldPwd: '',
          newPwd: '',
          confirmPwd: ''
        },
        pwdGrade: '',
        pwdSafeVerifySize: 'pwdSafeVerifySize',
        isRed: false,
        isOrange: false,
        isGreen: false,
        pwdSafeRedClass: 'pwdSafeRedClass',//
        pwdComplex: 'pwdComplex',
        pwdSafeOrangeClass: 'pwdSafeOrangeClass',//
        pwdSafeGreenClass: 'pwdSafeGreenClass',//
        resetPwdRules: {
          oldPwd: [
            {validator: Verify.validateOldPwd, trigger: 'blur'}
          ],
          newPwd: [
            {validator: Verify.validateNewPwd, trigger: 'blur'}
          ],
          confirmPwd: [
            {validator: validatePwd2, trigger: 'blur'}
          ],
        }
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle'])
    },
    created() {
      // this.ruleForm.telNumber = localStorage.getItem("phoneNumber");
      this.safeSettingInfo();

    },
    methods: {
      //
      safeSettingInfo(){
        let enterpriseId = localStorage.getItem("enterpriseId");
        var safeSettingInfo = '/api/enterprise/safeSetting/' + enterpriseId;
        let _self = this;
        _self.$get(safeSettingInfo, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            if(Verify.validateIsNull(res.data.data.username)){
              _self.loginAccount = res.data.data.username;
            }
            if(Verify.validateIsNull(res.data.data.enterpriseId)){
              _self.accountID = res.data.data.enterpriseId;
            }
            if(Verify.validateIsNull(res.data.data.phoneNumber)){
              _self.safeTel = res.data.data.phoneNumber;
              _self.ruleForm.telNumber = res.data.data.phoneNumber; //
            }
            if(Verify.validateIsNull(res.data.data.email)){
              _self.safeEmail = res.data.data.email;
            }
            _self.registerTime = Verify.convertDate(res.data.data.createdAt);
            _self.pwdStatus = res.data.data.passwordSetting;
            _self.safeTelStatus = res.data.data.phoneNumberSetting;//
            _self.safeEmailStatus = res.data.data.emailStatus;//
            // console.log("safeSetting==" + JSON.stringify(res.data.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      //
      modifyBindTel() {
        this.getImgCode();
        this.whether = !this.whether;
        this.isSelectedModifyTel = true;
        this.safeSetting = ">";
      },
      //
      modifyBindPwd() {
        this.isSelectedModifyPwd = true;
        this.whether = !this.whether;
        this.safeSetting = ">";
        },
      //
      modifyBindEmail() {
        this.isSelectedModifyEmail = true;
        this.whether = !this.whether;
        this.safeSetting = ">";
      },

      //
      confirmModifyNewTel() {
        let enterpriseId = localStorage.getItem("enterpriseId");
        let modifyTelUrl = "/api/enterprise/phone/" + enterpriseId;
        this.$put(modifyTelUrl, {
          mobile: this.ruleForm.phoneNumber,
          captcha: this.ruleForm.graphCode,
          smsCode: this.ruleForm.messageCode.toString(),
          enterpriseId: enterpriseId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            swal("", "", "success");
            this.safeSettingInfo();
            this.isSelectedModifyTel = false;
            this.whether = false;
            this.safeSetting = ">";
            this.$refs['ruleForm'].resetFields();
          } else {
            swal("",res.data.msg, "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });

      },
      //
      confirmModifyPwd() {
        let enterpriseId = localStorage.getItem("enterpriseId");
        let modifyPwdUrl = "/api/enterprise/password/" + enterpriseId;
        this.$put(modifyPwdUrl, {
          originalPassword: this.$md5(this.resetPwdForm.oldPwd),
          newPassword: this.$md5(this.resetPwdForm.newPwd),
          enterpriseId: enterpriseId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            swal("", "", "success");
            this.safeSettingInfo();
            this.$refs['resetPwdForm'].resetFields();//
            this.isSelectedModifyPwd = false;
            this.whether = false;
            this.safeSetting = ">";
            //
            // this.$router.push({ path: '/'})
          } else {
            swal("",res.data.msg, "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },
      //
      confirmBindEmail() {
        let enterpriseId = localStorage.getItem("enterpriseId");
        let modifyEmailUrl = "/api/enterprise/email/" + enterpriseId;
        this.$put(modifyEmailUrl, {
          email: this.emailForm.email,
          // enterpriseId: enterpriseId,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.safeSettingInfo();
            this.$refs['emailForm'].resetFields();
            swal("", "", "success");
            this.isSelectedModifyEmail = false;
            this.whether = false;
            this.safeSetting = ">";

          } else {
            swal("", res.data.msg, "error");
          }
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },

      getImgCode() {
        this.$get('/api/service/captcha', {
          width: '',
          height: '',
        }).then(res => {
          // console.log("test" + res);
          if (res.status == 200) {
            this.svg = res.data;
            this.isShow = false;
          } else {
            alert(res.statusText);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      refreshImgCode() {
        this.getImgCode();
      },
      getMessageCode() {
        this.$get('/api/service/sms', {
          captcha: this.ruleForm.graphCode,
          mobile: this.ruleForm.phoneNumber,
        }).then(res => {
          // console.log("test" + JSON.stringify(res));
          if (JSON.stringify(res.data.code) == 200) {
            this.$message({
              message: '',
              type: 'success'
            });
            // console.log("Msg test" + JSON.stringify(res.data.data));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
    },
    watch: {
      //
      resetPwdForm: {
        handler(newName) {
          if (newName.newPwd.length > 0) {
            let modes = Verify.pwdComplexityCheck(newName.newPwd);
            if (modes == 1) {
              this.isRed = true;
              this.isOrange = false;
              this.isGreen = false;
              this.pwdGrade = '';
            } else if (modes == 2) {
              this.isRed = true;
              this.isOrange = true;
              this.isGreen = false;
              this.pwdGrade = '';
            } else if (modes == 3) {
              this.isRed = true;
              this.isOrange = true;
              this.isGreen = true;
              this.pwdGrade = '';
            } else {
              this.isRed = false;
              this.isOrange = false;
              this.isGreen = false;
            }
          } else {
            this.isRed = false;
            this.isOrange = false;
            this.isGreen = false;
          }
        },
        deep: true,
        immediate: true
      }
    }
    }
</script>

<style scoped>
  @import "../../styles/personal/personal-center.scss";

  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .el-breadcrumb {
    font-size: 18px;
    line-height: 3.5;
    color: #333333;
  }

  .left_title {
    color: #666666;
    font-size: 14px;
  }

  .modify_btn {
    width: 120px;
    background-color: #33B2E8;
    color: #FFFFFF;
    font-size: 17px;
  }

  .modifyClass {
    display: none;
  }

  .unbind {
    color: #009BDD;
    font-size: 14px;
  }

  .bind_email_div {
    height: 563px;
    margin-top: 10px;
  }

  /**/
  .verify_hint {
    font-size: 12px;
    color: #666666;
    text-align: left;
  }

  /**/
  .verify_star {
    font-size: 12px;
    color: #009BDD;
  }

  .confirm_btn {
    width: 306px;
    background-color: #33B2E8;
    color: #FFFFFF;
    font-size: 17px;
  }

  /**/
  .login_btn {
    width: 306px;
    height: 43px;
    background-color: #33B2E8;
    text-align: center;
    /*padding-left: 22px;*/
    /*padding-right: 22px;*/
  }

  .img_code_height {
    height: 40px;
  }

  .refresh_img_code {
    font-size: 10px;
    line-height: 12px;
    color: red;
  }

  .input_width {
    width: 460px;
  }
  /**/
  .width360 {
    width: 360px;
  }

  .width305 {
    width: 305px;
    margin-top: 5px;
  }

  .pwdSafeRedClass {
    background: #E60012;
  }

  .pwdSafeOrangeClass {
    background: #F39800;
  }

  .pwdSafeGreenClass {
    background: #5FBF4D;
  }

  .pwd_safe_margin_top {
    margin-top: 5px;
  }

  .nextStep {
    width: 288px;
    height: 43px;
    margin: 0 auto;
  }

  .btn_color {
    color: #FFFFFF;
    background-color: #33B2E8;
  }

  .pwdComplex {

    background: #C0C0C0;
  }

  .pwdSafeVerifySize {
    width: 55px;
    height: 8px;
  }

  .pwd_safe_prompt {
    font-size: 12px;
    color: #666666;
    line-height: 0.8;
  }

  .pwd_specification {
    white-space: normal; /**/
    font-size: 12px;
    color: #33B2E8;
    border: 1px solid #33B2E8;
    background: rgba(190, 233, 250, 1);
    width: 115px;
    height: 38px;
    float: right;
    margin-right: 44%;
    margin-top: -22.7%;
  }

  .el-input__inner {
    width: 305px;
  }
  .modify{
    display: block;
  }
</style>



