<template>
  <div class="margin_left_right white_bg_color">
    <div>
      <hr class="hr_color">
      <el-row>
        <el-col :span="6">
          <!--<div class="border"></div>-->
          <img :src=introImageUrl class="logo_img"/>
        </el-col>
        <el-col :span="18" class="text_align_left padding_left10">
            <div>
              <p class="stores_name">{{storesName}}</p>
              <p class="regular_font">
                <img class="icon_img" src="../../assets/medicalproduct/didian_tjcp_jgjs@2x.png" height="18"
                     width="18"/>
                <span>{{storesAddress}}</span>
              </p>
              <p class="regular_font">
                <img class="icon_img" src="../../assets/medicalproduct/dianhua_tjcp_jgjs@2x.png" height="18"
                     width="18"/>
                {{storePhone}}</p>
              <p class="regular_font">
                <img class="icon_img" src="../../assets/medicalproduct/shijian_tjcp_jgjs@2x.png" height="18"
                     width="18"/>
                {{businessHours}}
              </p>
              <p class="regular_font">

                <img class="icon_img" src="../../assets/medicalproduct/zhuyi_tjjg_jgxx@2x.png" height="18"
                     width="18" v-if="needingAttention !==null"/>
                {{needingAttention}}
              </p>
              <p class="regular_font">
                <img class="icon_img" src="../../assets/medicalproduct/jianjie_tjjg_jgxx@2x.png" height="18"
                     width="18"/>
                <span class="special_font">{{suitNumber}}</span><span class="special_font">{{suitPrice}}</span>
              </p>
            </div>
          <el-row class="margin_top15">
            <el-col :span="6">
              <el-button size="medium" class="select_suit" :class="{clickFont:active}" @click="selectSuit()">
              </el-button>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
    <div class="margin_top15">
      <el-row :gutter="25">
        <el-col :span="19">
          <el-tabs v-model="activeName" type="card" @tab-click="handleClick" id="tabs">
            <el-tab-pane label="" name="first">
              <div>
                <div class="store_info">
                  <el-row v-for="(medical,index) in medicalPackage" :key="index">
                    <el-col :span="6" class="padding_left10">
                      <!--<div class="institution_img">-->
                        <img :src=medical.packageImgUrl class="institution_img"/>
                      <!--</div>-->
                    </el-col>
                    <el-col :span="18" class="text_align_left">
                      <div>
                        <p class="stores_name">{{medical.name}}<span @click="packageDetailEvent(medical.productId)"><u class="special_font select_detail"></u></span>
                        </p>
                        <p class="margin_top0_bottom0"><span class="discount_price"></span><span
                          class="discount_price">{{medical.CatalogProduct.sale_price === null ?'0.00':medical.CatalogProduct.sale_price}}</span><span class="origin_price"></span>&emsp;&emsp;&emsp;<span
                          class="origin_price line"></span><span class="origin_price line">{{medical.CatalogProduct.market_price === null?'0.00':medical.CatalogProduct.market_price}}</span></p>
                        <p class="package_introduce">
                          {{medical.description}}
                        </p>
                      </div>
                    </el-col>
                  </el-row>
                </div>
              </div>
              <el-pagination
                background
                layout="prev, pager, next"
                :total=packageTotal v-if="packageTotal>0">
              </el-pagination>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
              <div>
                <div class="store_info">
                  <el-row>

                  </el-row>
                </div>
                <div class="text_align_left padding_left10">
                  <p class="stores_name"></p>
                  <p class="stores_introduce">
                   {{description}}
                  </p>
                  <!--<p class="stores_introduce">-->
                    <!--9106000;;;-->
                  <!--</p>-->
                </div>
                <div class="text_align_left padding_left10">
                  <p class="stores_name"></p>
                  <p class="stores_introduce">{{storesAddress}}</p>
                  <div>
                    <img :src=introImageUrl height="402"/></div>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-col>
        <el-col :span="5">
          <div class="float_left regular_font">
            
          </div>
          <el-col :span="20" v-for="(recommend, index) in recommendData" :key="index">
            <el-card :body-style="{ padding: '0px' }">
              <img :src=recommend.imgUrl class="recommend_image">
              <div class="card_content card_align">
                <p class="card_title">{{recommend.CatalogProduct.name}}</p>
                <span class="card_subtitle">{{recommend.CatalogProduct.simpleDescription}}</span>
                <p class="margin_top0_bottom0"><span class="discount_price"></span><span
                  class="discount_price">{{recommend.CatalogProduct.salePrice}}</span><span
                  class="origin_price"></span>&emsp;&emsp;&emsp;<span
                  class="origin_price line" v-if="recommend.CatalogProduct.marketPrice !== null"></span><span
                  class="origin_price line">{{recommend.CatalogProduct.marketPrice}}</span></p>
              </div>
            </el-card>
          </el-col>
        </el-col>
      </el-row>
    </div>
  </div>
  
</template>
<script>
  import Verify from '../../utils/utils';
  export default {
    data() {
      return {
        introImageUrl:'',
        needingAttention:'',
        suitNumber:0,
        suitPrice:0,
        suitName:'',
        suitSex: 0,//1 2 0
        salePrice: '',
        marketPrice: '0',
        medicalCenter:'',
        packageDescription: '....',//
        institutionId: '',//id
        active: false,
        activeName: 'first',
        storesName: '',
        storesAddress: '',
        storePhone: '4001-1211-00',
        businessHours: '8:00-11:3013:00-17:00',
        institutionLocate: '',
        description:'',
        addressData:'',//json
        medicalPackage:[],//
        packageTotal:0,
      //  
        introIntroduce:'',
        introImageUrl:'',//
        recommendData:[],//
      }
    },
    created() {
      this.getInstitutionInfo();//
      this.getCityData(); //json
      this.getRecommendList();//
    },
    methods: {
      getInstitutionInfo() {
        let url = '/api/medical/centers/' + this.$route.query.institutionId;
        this.$get(url, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("detail==" + JSON.stringify(res.data));
            let institutionDetail = res.data.data;
            this.businessHours = institutionDetail.businessHours;
            this.storePhone = institutionDetail.telephone;
            let introProvince = institutionDetail.province;
            let introCity = institutionDetail.city;
            let introCounty = institutionDetail.county;
            //
            if (introProvince.startsWith('1') || introProvince.startsWith('2') || introProvince.startsWith('3') ||introProvince.startsWith('4')||
              introProvince.startsWith('5')||introProvince.startsWith('6')||introProvince.startsWith('7')||introProvince.startsWith('8')){
              this.storesAddress = this.addressData['86'][introProvince]+ this.addressData[introProvince][introCity]+this.addressData[introCity][introCounty];
            }else{
              this.storesAddress =  introProvince + introCity +introCounty;
            }
            if (Verify.validateIsNull(institutionDetail.CatalogManufacturer.File)) {
              //logo
              this.introImageUrl = 'http://'+institutionDetail.CatalogManufacturer.File.manufacturer;
            }
            // this.storesAddress = institutionDetail.province + institutionDetail.city;
            this.needingAttention = institutionDetail.note;
            this.suitNumber = institutionDetail.packages;
            //
            if (Verify.validateIsNull(institutionDetail.productLowerPrice)) {
              this.suitPrice = institutionDetail.productLowerPrice;
            }
            this.description = institutionDetail.introduce;
            this.institutionLocate = institutionDetail.address;
            let catalogManufacturer = institutionDetail.CatalogManufacturer;
            this.storesName = catalogManufacturer.name; //

            //
            let examinationPackages = institutionDetail.examinationPackages;
            this.medicalPackage = examinationPackages.items;
            this.packageTotal = examinationPackages.total;
            //
            for(let i=0;i<examinationPackages.items.length;i++){
              //
              if (Verify.validateIsNull(examinationPackages.items[i].CatalogProduct.File)) {
                this.medicalPackage[i]['packageImgUrl'] = 'http://'+examinationPackages.items[i].CatalogProduct.File.productImg;
              }
            }
            //
            let institutionIntroduce = institutionDetail.MedicalOrganization;
            this.introIntroduce = institutionIntroduce.description;
            if (Verify.validateIsNull(institutionDetail.LocationFile)) {
              //
              this.introImageUrl = 'http://'+institutionDetail.LocationFile.url;
            }
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      cartHandleChange(value) {
        console.log(value);
      },
      //
      selectSuit() {
        this.active = true;
        document.getElementById('tabs').scrollIntoView();
      },
      //
      packageDetailEvent(id){
        this.$router.push({ path: '/MedicalProductDetail',query:{productId:id}});
      },
      handleClick(tab, event) {
        console.log(tab, event);
      },
      // table header
      tableHeaderColor({row, column, rowIndex, columnIndex}) {
        if (rowIndex === 0) {
          return 'background-color: #F6F6F6;color: #404040;font-weight: 500;'
        }
      },
      // 
      getRecommendList() {
        let product = '/api/ad/products/recommendMedicalExaminationPackage';
        this.$get(product, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("test" + JSON.stringify(res.data.data));
            this.recommendData = res.data.data;
            for (let i = 0; i < this.recommendData.length; i++) {
              if (Verify.validateIsNull(this.recommendData[i].CatalogProduct.File)) {
                this.recommendData[i]['imgUrl'] = "http://" + this.recommendData[i].CatalogProduct.File.url;
              }
            }
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      //json
      getCityData(){
        const config = require('../../../static/region.json');
        this.addressData = config;
      }
    }
  }
</script>
<style>
  @import "../../styles/personal/personal-center.scss";

  .margin_left_right {
    margin-left: 10%;
    margin-right: 10%;
  }

  .hr_color {
    height: 2px;
    background-color: #e6e6e6;
    border: none;
  }

  .border {
    margin-top: 10px;
    border: 1px solid #e6e6e6;
    background-color: #EEEEEE;
    width: 280px;
    height: 220px;
  }

  .title_font {
    color: #333333;
    font-size: 18px;
    margin-top: 8px;
    margin-bottom: 8px;
  }

  .special_font {
    color: #009BDD;
    font-size: 14px;
  }

  .sub_font {
    color: #a2a2a2;
    font-size: 14px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .second_font {
    color: #666666;
    font-size: 16px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .regular_font {
    color: #666666;
    font-size: 14px;
    margin-top: 6px;
    margin-bottom: 6px;
  }

  .select_suit {
    background-color: #33B2E8;
    color: #FFFFFF;
    /*border: 1px solid #DF2321;*/
  }

  .clickFont {
    background-color: #DF2321;
    color: #ffffff;
  }

  .tab_bg_color {
    border: 1px solid rgba(238, 238, 238, 1);
    background-color: #EEEEEE;
  }

  /**/
  .institution_img {
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid #e6e6e6;
    background-color: #EEEEEE;
    width: 200px;
    height: 150px;
  }

  .store_info {
    border: 1px solid rgba(238, 238, 238, 1);
  }

  .icon_img {
    vertical-align: middle;
  }

  .stores_name {
    font-size: 16px;
    color: #404040;
  }

  .stores_introduce {
    font-size: 12px;
    color: #666666;
  }

  .select_detail {
    float: right;
    padding-right: 10px;
  }
  .package_introduce{
    font-size: 14px;
    color: #a2a2a2;
  }
  .logo_img{
    width:220px;
    height:220px;
    /*background:#EEEEEE;*/
  }
  .recommend_image{
    width:199px;
    height:198px;
  }
</style>





