<script>
  // import InvoiceInfo from '../../firm/costmanagement/InvoiceInfoManage'
  import Verify from '../../../utils/utils';
  import swal from 'sweetalert2'; //alert
  import {mapGetters} from 'vuex'
  export default {
    name: "InvoiceManagement",
    components: {
      // 'invoice-info': InvoiceInfo
    },
    data() {
      return {
        applyAndList: false,//
        orderKey:"1",
        isSelect: true, //'1''2'
        invoiceFont: 'invoice_font',
        defaultFont: 'default_font',
        selectStepBtn: 'select_step_btn',
        stepBtn: 'step_btn',
        borderColor: 'border_color',
        selectBorderColor: 'select_border_color',
        activeIndex: '1',
        orderNumber: '1',
        orderMoneyTotal: '200',
        selectReceipts: '10',
        invoiceAmount: '1000',
        tableData: [],
        invoiceAddressData: [],
        orderListData:[],
        currentPage: 1,
        total: 1,
        invoiceName: '***',//
        invoiceType: '',
        invoiceNature: '',
        addressNumber: '1',
        selectInvoiceOrder: false, //
        confirmInvoiceInfo: true, //
        apply: false, //
        orderListFlag:true,//
        modifyInvoiceInfo: false,
        checked: true,
        addAddressFormVisible: false,
        addInvoiceAddress:{
          receiver: '',
          tel: '',
          province: '',
          city: '',
          region: '',
          address: '',
          desc: '',
        },
        editInvoiceAddress:[],
        formLabelWidth: '100px',
        invoiceInfoShow: true,//
        invoiceApplySuccess: true,//
        cities: [],
        //
        invoiceInfo: {
          accountType:"0",
          invoiceName: '',
          invoiceType: '0',//0 1
          taxRegistrationNo: '',
          bankName: '',
          bankAccountNumber: '',
          registerAddress: '',
          registeredLine: '',//
        },
        invoiceRules: {
            invoiceName: [
              {required: true, message: '', trigger: 'blur'},
              {min: 2, message: '2', trigger: 'blur'}
            ],
            // birth: [
            //   { type: 'date', required: true, message: '', trigger: 'change' }
            // ],
          },
      //  
        invoiceNumber:271311,
        invoiceMoney:1220,
        deInvoiceNature:'',
        deInvoiceState:'',
        deInvoiceType:'',
        deInvoiceHead:'XXXX',
        deApplyTime:'2019-01-28',
        deOrderNum:100,
        showInvoiceDetail:false,
        addressData:[],
      }
    },
    computed: {
      ...mapGetters(['asideNavTitle','invoiceInfoFlag'])
    },
    created() {
      this.getCityData();
      //
      this.getInvoiceAddressData();
      // 
      this.getInvoiceBaseInfo();
      // 
      this.getInvoiceOrderList();
      //
      this.getOrderList();
      // console.log("invoiceInfoFlag=="+this.invoiceInfoFlag);
      // if (this.invoiceInfoFlag == true){
      //   this.confirmInfo();
      // }
    },
    methods: {
      selectProvinceEvent(key){
        var numReg = /^[0-9]+$/;
        var numRe = new RegExp(numReg);
        if (key != undefined && numRe.test(key)) {
          var arr = [];
          for(let i in this.addressData[key]){
            arr.push(i)
          }
          this.addInvoiceAddress.city = arr[0];
          var county = [];
          for (let j in this.addressData[this.addInvoiceAddress.city]) {
            county.push(j);
          }
          this.addInvoiceAddress.region = county[0];
          // this.detailAddress = this.addressData['86'][this.selectedProvince];
        }
      },
      selectCityEvent(val){
        var numReg = /^[0-9]+$/;
        var numRe = new RegExp(numReg);
        if (val != undefined && numRe.test(val)) {
          var arr = [];
          for(let i in this.addressData[val]){
            arr.push(i)
          }
          this.addInvoiceAddress.region = arr[0];
          // this.detailAddress += this.addressData[this.selectedProvince][this.selectedCity];
        }
      },
      getInvoiceAddressData() {
        let enterpriseId = localStorage.getItem("enterpriseId");
        let listUrl = "/api/user/invoiceAddress/" + enterpriseId;
        this.$get(listUrl, {}).then(res => {
          console.log(JSON.stringify(res))
          if (JSON.stringify(res.data.code) == 200) {
            this.invoiceAddressData = res.data.data.items;
            // console.log("data ==" + JSON.stringify(res));
          } else {
            //JSON.stringify(res.data.message)
            swal("", res.data.msg, "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },
      getInvoiceBaseInfo(){
        let enterpriseId = localStorage.getItem("enterpriseId");
        let listUrl = "/api/enterprise/invoice/basic/" + enterpriseId;
        this.$get(listUrl, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            console.log("Base info data ==" + JSON.stringify(res));
          } else {
            //JSON.stringify(res.data.message)
            swal("", JSON.stringify(res.data.message), "error");
          }
          console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },
      // 
      getInvoiceOrderList(){
        let enterpriseId = localStorage.getItem("enterpriseId");
        let listUrl = "/api/enterprise/invoice/orders/" + enterpriseId;
        this.$get(listUrl, {}).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.tableData = res.data.data.items;
            for (const i in this.tableData) {
              let createDate = Verify.convertDate(this.tableData[i].paidDate);
              this.tableData[i]['createDate'] = createDate;
            }
            console.log("Invoice order list data ==" + JSON.stringify(res));
          } else {
            //JSON.stringify(res.data.message)
            swal("", "", "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },
      //
      getOrderList(){
        let enterpriseId = localStorage.getItem("enterpriseId");
        let listUrl = "/api/enterprise/invoices/" + enterpriseId;
        this.$get(listUrl, {
          accountId:enterpriseId
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.orderListData = res.data.data.items;
            for (const i in this.orderListData) {
              //invoiceDate 
              let createDate = Verify.convertDate(this.tableData[i].invoiceDate);
              this.tableData[i]['createDate'] = createDate;
            }
            console.log("order list data ==" + JSON.stringify(res));
          } else {
            //JSON.stringify(res.data.message)
            swal("", "", "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },
      //
      lastStep() {
        this.invoiceInfoShow = true;
        this.confirmInvoiceInfo = true;
        this.selectInvoiceOrder = false;
        this.isSelect = true;
        this.invoiceInfoShow = true; //
        this.invoiceApplySuccess = true; //
      },
      //
      addAddress() {
        this.addAddressFormVisible = true;
      },
      //
      sureAddInvoiceAddress() {
        if (!Verify.phoneNumberVerify(this.addInvoiceAddress.tel)) {
          alert("");
        } else if (Verify.validateIsNull(this.addInvoiceAddress.receiver) &&
          Verify.validateIsNull(this.addInvoiceAddress.province) &&
          Verify.validateIsNull(this.addInvoiceAddress.city) &&
          Verify.validateIsNull(this.addInvoiceAddress.region) &&
          // Verify.validateIsNull(this.addInvoiceAddress.address) &&
          Verify.validateIsNull(this.addInvoiceAddress.desc)) {
          let enterpriseId = localStorage.getItem("enterpriseId");
          let addUrl = "/api/user/invoiceAddress/" + enterpriseId;
          this.$post(addUrl, {
            receiver: this.addInvoiceAddress.receiver,
            phoneNumber: this.addInvoiceAddress.tel,
            province: this.addInvoiceAddress.province,
            city: this.addInvoiceAddress.city,
            county: this.addInvoiceAddress.region,
            address: this.addInvoiceAddress.address,//
            addressName: this.addInvoiceAddress.desc,//
            default: this.checked,//1 0
          }).then(res => {
            console.log(JSON.stringify(res))
            if (JSON.stringify(res.data.code) == 200) {
              this.addAddressFormVisible = false;
              this.getInvoiceAddressData();
              console.log("invoice data==" + JSON.stringify(res.data));
            } else {
              swal(JSON.stringify(res.data.message), "", "error");
            }
            // console.log("res=="+JSON.stringify(res))
          }).catch(function (reason) {
            console.log(JSON.stringify(reason));
          });
        } else {
          alert(",");
        }
      },
      //
      deleteClick(row) {
        swal({
          title: "",
          text: "",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "",
          cancelButtonText: ""
        }).then((result) => {
          if (result.value) {
            let addressId = row.addressId;
            let deleteAddressUrl = "/api/user/invoiceAddress/" + addressId;
            this.$del(deleteAddressUrl, {
              // addressId: addressId
            }).then((res) => {
              if (JSON.stringify(res.data.code) == 200) {
                swal("", "", "success");
              } else {
                swal("", JSON.stringify(res.data.message), "error");
              }
              this.getInvoiceAddressData();
            })
          } else {
            swal("Cancelled", "", "");
          }
        });
      },
      //
      defaultClick(row) {
        console.log("row==" + JSON.stringify(row));
        let str = "?";
        if (row.default = true) {
          str = "";
        }
        swal({
          title: str,
          text: "",
          type: "question",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "",
          cancelButtonText: ""
        }).then((result) => {
          if (result.value) {
            let addressId = row.addressId;
            console.log(addressId+"==="+JSON.stringify(this.checked))
            let setDefaultAddUrl = "/api/user/invoiceAddress/" + addressId + "/default";
            this.$put(setDefaultAddUrl, {
              default: !this.checked,
              // addressId: addressId
            }).then((res) => {
              console.log("adminId==" + JSON.stringify(res) + row + JSON.stringify(row));
              if (JSON.stringify(res.data.code) == 200) {
                  swal("", "", "success");
              } else {
                swal("", res.data.msg, "error");
              }
              this.getInvoiceAddressData();
            })
          } else {
            swal("Cancelled", "", "");
          }
        });
      },
      //
      editInfo(row) {
        // console.log("123" + JSON.stringify(row));
        this.editInvoiceAddress = row;
        this.modifyInvoiceInfo = true;
      },
      //
      sureModifyInvoiceInfo() {
        this.modifyInvoiceInfo = false;
        let addressId = this.editInvoiceAddress.addressId;
        let deleteAddressUrl = "/api/user/invoiceAddress/" + addressId;
        this.$put(deleteAddressUrl, {
          // addressId: addressId,
          receiver: this.editInvoiceAddress.receiver,
          phoneNumber: this.editInvoiceAddress.phoneNumber,
          province: this.editInvoiceAddress.province,
          city: this.editInvoiceAddress.city,
          county: this.editInvoiceAddress.county,
          address: this.editInvoiceAddress.address,//
          addressName: this.editInvoiceAddress.name,//
          default: this.editInvoiceAddress.default,//1 0
        }).then((res) => {
          console.log("==" + JSON.stringify(JSON.stringify(res.data)))
          deleteAddressUrl
          if (JSON.stringify(res.data.code) == 200) {
            this.modifyFormVisible = false;
            swal("", "", "success");
          } else {
            swal(JSON.stringify(res.data.message), "", "error");
          }
          this.getInvoiceAddressData();
        })
      },
      //
      settingInvoiceInfo() {
        this.applyAndList = true;//
        this.apply = true;//"1" "2"
        this.invoiceInfoShow = false;//
        this.selectInvoiceOrder = true;//'1'
        this.confirmInvoiceInfo = true;//'2'
        this.isSelect = false;//"2"
        this.invoiceApplySuccess = true; //
      },
      //
      confirmSetInvoiceInfo(){
        let enterpriseId = localStorage.getItem("enterpriseId");
        let listUrl = "/api/enterprise/invoice/basic/" + enterpriseId;
        this.$post(listUrl, {
          accountType:this.invoiceInfo.accountType,
          invoiceHead:this.invoiceInfo.invoiceName,
          invoiceType: this.invoiceInfo.invoiceType,// 0 1
          taxpayerId: this.invoiceInfo.taxRegistrationNo,//
          bankAccount: this.invoiceInfo.bankAccountNumber,//  invoiceType=1 
          bankName: this.invoiceInfo.bankName,
          address: this.invoiceInfo.registerAddress,
          phoneNumber: this.invoiceInfo.registeredLine,
        }).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            // debugger
            swal("","", "success");
            this.confirmInfo();
          } else {
            swal("", JSON.stringify(res.data.message), "error");
          }
          // console.log("res=="+JSON.stringify(res))
        }).catch(function (reason) {
          console.log(JSON.stringify(reason));
        });
      },
      //
      confirmInfo() {
        this.applyAndList = false; //
        this.apply = false;//"1" "2"
        this.confirmInvoiceInfo = true;//'2'
        this.selectInvoiceOrder = false;//'1'
        this.isSelect = true;//"2"
        this.invoiceInfoShow = true; //
        this.invoiceApplySuccess = true; //
      },
      //
      invoiceOrder() {
        this.invoiceInfoShow = true;
        this.confirmInvoiceInfo = true;
        this.selectInvoiceOrder = false;
        this.isSelect = true;
        this.invoiceInfoShow = true; //
        this.invoiceApplySuccess = true; //
      },
      //
      invoiceList() {
        this.applyAndList = false;//
        this.apply = false;//"1" "2"
        this.invoiceInfoShow = true;
        this.confirmInvoiceInfo = false;
        this.selectInvoiceOrder = true;
        this.isSelect = false;
        this.invoiceApplySuccess = true; //
      },

      //
      applyInvoice() {
        this.applyAndList = true;//
        this.apply = true;//"1" "2"
        this.invoiceInfoShow = true;//
        this.selectInvoiceOrder = true;//'1'
        this.confirmInvoiceInfo = true;//'2'
        this.isSelect = false;//"2"
        this.invoiceApplySuccess = false; //
      },
      // 
      goInvoiceList() {
        this.applyAndList = false; //
        this.apply = false;//"1" "2"
        this.invoiceInfoShow = true; //
        this.selectInvoiceOrder = true;//'1'
        this.confirmInvoiceInfo = false;//'2'
        this.isSelect = false;//"2"
        this.invoiceApplySuccess = true; //
      },

      //
      handleSelect(key, keyPath) {
        if (keyPath == "2") {
          this.apply = true;
          this.orderListFlag = false;
        } else {
          this.apply = false;
          this.orderListFlag = true;
          // console.log(key, keyPath);
        }
        this.orderKey = keyPath;
      },
      editInvoiceInfo() {


      },
      //
      detailClick(){
        this.orderListFlag = true;//
        this.showInvoiceDetail = true;
        this.applyAndList = true; //
        this.apply = true;//"1" "2"
        this.invoiceInfoShow = true; //
        this.selectInvoiceOrder = true;//'1'
        this.confirmInvoiceInfo = false;//'2'
         // this.isSelect = false;//"2"
        this.invoiceApplySuccess = true; //
        // this.$router.push({ path: '/InvoiceDetail'})
      },
      //
      returnInvoiceManage(){
        this.showInvoiceDetail = false;
        this.orderListFlag = false;//
        this.applyAndList = false; //
        this.apply = true;//"1" "2"
        this.invoiceInfoShow = true; //
        this.selectInvoiceOrder = false;//'1'
        this.confirmInvoiceInfo = false;//'2'
        this.isSelect = true;//"2"
        this.invoiceApplySuccess = true; //
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currentPage = val;
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      // table header
      tableHeaderColor({row, column, rowIndex, columnIndex}) {
        if (rowIndex === 0) {
          return 'background-color: #F6F6F6;color: #404040;font-weight: 500;'
        }
      },
      getCityData() {
        const config = require('../../../../static/region');
        // this.firmAddress = config['86'];
        this.addressData = config;
      },
    }
  }
</script>

<style scoped>
  @import "../../../styles/personal/personal-center.scss";

  .header_bg {
    background-color: white;
    text-align: right;
    font-size: 18px;
    color: #333333;
  }

  .el-breadcrumb {
    font-size: 18px;
    line-height: 3.5;
    color: #333333;
  }

  /*"1""2"*/
  .step_btn {
    background-color: #E6EBEE;
    color: #ffffff;
    width: 40px;
  }

  .select_step_btn {
    background-color: #33B2E8;
    color: #ffffff;
    width: 40px;
  }

  .striping {
    display: inline-block;
    position: relative;
    top: -6px;
    content: "";
    width: 100px;
    height: 0px;
  }

  .border_color {
    border: 1px solid #404040;
  }

  .select_border_color {
    border: 1px solid #33B2E8;
  }

  .invoice_font {
    color: #33B2E8;
    font-size: 14px;
  }

  .default_font {
    color: #404040;
    font-size: 14px;
  }

  .select_color {
    color: #33B2E8;
  }

  .step_explain {
    margin: 10px;
    background: rgba(246, 246, 246, 1);
    border: 1px solid rgba(189, 186, 187, 1);
  }

  .regular_font {
    color: #666666;
    font-size: 14px;
  }

  .setting_invoice_info {
    color: #009BDD;
    font-size: 14px;
  }

  .padding_top_bottom0 {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .apply_invoice {
    color: #ffffff;
    background-color: #33B2E8;
    font-size: 17px;
  }

  .content_font {
    color: #333333;
    font-size: 14px;
  }

  .last_step {
    width: 120px;
    color: #404040;
    font-size: 17px;
  }

  .displayClass {
    display: none;
  }

  /**/
  .success_div {
    height: 456px;
    margin-top: 15px;
    background-color: #ffffff;
  }

  .apply_success_img {
    vertical-align: middle;
  }

  .apply_success_font {
    font-size: 18px;
    color: #333333
  }
  /**/
  .data_form {
    width: 450px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .confirm_invoice_info{
    width:310px;
    background-color:#33B2E8;
    font-size: 17px;
    color: #FFFFFF;
  }
  /**/
  .p_line_height{
    line-height: 32px;
    margin-top: 0px;
  }
  .invoice_detail_div{
    border:1px solid rgba(204,204,204,1);
    /*margin-left: 10px;*/
    /*margin-right: 10px;*/
    margin: 10px;
    /*padding-left: 0px;*/
  }
  .invoice_detail_bar{
    text-align: left;
    background-color: #F6F6F6;
    height:36px;
  }
  .invoice_state_font{
    color: #5BC146;
    font-size: 14px;
  }
</style>



