<template>
  <div class="bg_color_gray">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="" name="first">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%" class="marginTop10">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            label="ID"
            prop="cardId">
          </el-table-column>
          <el-table-column
            label=""
            prop="cardNumber">
          </el-table-column>
          <el-table-column
            prop="packageName"
            label=""
            width="120">
          </el-table-column>
          <el-table-column
            prop="amount"
            label=""
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="organizationName"
            label=""
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop=""
            label="">
            <template slot-scope="scope">
              {{ scope.row.status=== 2 ? '' : '' }}
            </template>
          </el-table-column>

          <el-table-column
            fixed="right"
            label=""
            width="180">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="viewPasswordEvent(scope.row)"></el-button>
              <el-button type="text" size="small"  v-if="scope.row.status===0" @click="transferEvent(scope.row)"></el-button>
              <el-button type="text" size="small"  v-if="scope.row.status===0" @click="appointmentEvent(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-if="total>0">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="total, prev, pager, next"
            :total=total>
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%" class="marginTop10">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            label="ID"
            prop="cardId">
          </el-table-column>
          <el-table-column
            label=""
            prop="cardNumber">
          </el-table-column>
          <el-table-column
            prop="packageName"
            label=""
            width="120">
          </el-table-column>
          <el-table-column
            prop="amount"
            label=""
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="organizationName"
            label=""
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop=""
            label="">
            <template slot-scope="scope">
              {{ scope.row.status=== 2 ? '' : '' }}
            </template>
          </el-table-column>

          <el-table-column
            fixed="right"
            label=""
            width="180">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="viewPasswordEvent(scope.row)"></el-button>
              <el-button type="text" size="small" v-if="scope.row.status===0" @click="transferEvent(scope.row)"></el-button>
              <el-button type="text" size="small"  v-if="scope.row.status===0" @click="appointmentEvent(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-if="total>0">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="total, prev, pager, next"
            :total=total>
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="third">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%" class="marginTop10">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            label="ID"
            prop="cardId">
          </el-table-column>
          <el-table-column
            label=""
            prop="cardNumber">
          </el-table-column>
          <el-table-column
            prop="packageName"
            label=""
            width="120">
          </el-table-column>
          <el-table-column
            prop="amount"
            label=""
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="organizationName"
            label=""
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="status"
            label="">
            <template slot-scope="scope">
              {{ scope.row.status===2? '' : '' }}
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="180">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="viewPasswordEvent(scope.row)"></el-button>
              <el-button type="text" size="small"  v-if="scope.row.status===0" @click="transferEvent(scope.row)"></el-button>
              <el-button type="text" size="small"  v-if="scope.row.status===0" @click="appointmentEvent(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block" v-if="total>0">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="total, prev, pager, next"
            :total=total>
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>
    <el-dialog id="givenCardModal" ref="givenCardModal" title="" :visible.sync="cardDialogVisible">
      <el-form :model="givenCardForm" style="text-align: right">
        <el-form-item label="" class="text_align_left">
          <span v-model="phoneNumber" autocomplete="off">{{phoneNumber}}</span>
        </el-form-item>
        <el-form-item label="" prop="graphCode">
          <el-row>
            <el-col :span="8">
              <el-input style="" type="text" v-model="givenCardForm.graphCode" autocomplete="off"
                        placeholder=""></el-input>
            </el-col>
            <el-col :span="4">
              <!--<el-button class="img_code" @click="getImgCode()"></el-button>-->
              <div v-html="svg" v-show="!isShow" class="img_code_height">
                {{svg}}
              </div>
            </el-col>
            <el-col :span="4">
              <span class="refresh_img_code" @click="refreshImgCode()"></span>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="" prop="messageCode">
          <el-row>
            <el-col :span="8">
              <el-input v-model="givenCardForm.messageCode" placeholder=""></el-input>
            </el-col>
            <el-col :span="4">
              <el-button @click="getMessageCode(givenCardForm.graphCode)"></el-button>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cardDialogVisible = false"> </el-button>
        <el-button type="primary" @click="getCardPassward()"> </el-button>
      </div>
    </el-dialog>
  </div>

</template>

<script>
  import VerifyUtils from '../../utils/utils'
  import swal from 'sweetalert2'; //alert
  export default {
    name: "MedicalCard",
    data() {
      return {
        keyword: '',
        activeName: 'first',
        tableData: [],
        currentPage: 1,
        total: 1,
        cardInfo:[],
        cardDialogVisible:false,
        phoneNumber: '',
        givenCardForm: {
          graphCode: '',
          messageCode: '',
        },
        svg: '',
        showImgCode: '',
        isShow: true,
        //
        cooperated: 1,
        unCoopTableData: [],
        unCoopCurrentPage: 1,
        unCoopTotal: 1,
        unCooperated: 0,
        allTableData: [],
        allCurrentPage: 1,
        allTotal: 1,
        allCooperated: 1,
        firmListInfo: [],//
      }
    },
    created() {
      this.getImgCode();
      this.phoneNumber = localStorage.getItem("phoneNumber");
      if (this.activeName == "second") {
        this.getCardListData(0)
      } else if (this.activeName == "first") {
        this.getCardListData(1)
      } else {
        this.getCardListData()
      }
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
        if (tab.name == "second") {
          //
          this.getCardListData(2)
        } else if (tab.name == "first") {
          //
          this.getCardListData(0)
        } else {
          //
          this.getCardListData(3)
        }
      },
      getCardListData(status) {
        let _self = this;
        var data = {
          page: _self.currentPage,
          perPage: 10,
        };
        let userId = localStorage.getItem("userId");
        let givenUrl = '/api/personal/' + userId + '/' + status + '/phyCards';
        this.$get(givenUrl, data).then(res => {
          if (JSON.stringify(res.data.code) == 200) {
            this.tableData = res.data.data.items;
            console.log("status===" + JSON.stringify(res.data.data.items));
          } else {
            alert(res.data.msg);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });

      },
      //
      viewPasswordEvent(rowData) {
        this.cardInfo = rowData;
        this.givenCardForm.graphCode = '';
        this.givenCardForm.messageCode = '';
        this.cardDialogVisible = true;
      },
      //
      getCardPassward(){
        let _self = this;
        let url = '/api/personal/'+ _self.cardInfo.cardId +'/phyCards/password';
        let data = {
          mobile: this.phoneNumber,
          captcha:_self.givenCardForm.graphCode,
          smsCode:_self.givenCardForm.messageCode,
        };
        if (VerifyUtils.validateIsNull(_self.givenCardForm.graphCode) &&
          VerifyUtils.validateIsNull(_self.givenCardForm.messageCode)) {
          _self.$get(url, data).then(res => {
            if (JSON.stringify(res.data.code) == 200) {
              console.log("" + JSON.stringify(res.data.data.items));
              swal("", "", "success");
            } else {
              debugger
              alert(res.data.msg);
            }
          }).catch(function (reason) {
            console.log('catch:', reason);
          });
        }else{
          swal("", "", "error");
        }
      },
      //
      transferEvent() {

      },
      //
      appointmentEvent() {

      },
      deleteEvent(row) {
        swal({
          title: "?",
          text: "",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "",
          cancelButtonText: ""
        }).then((result) => {
          if (result.value) {
            var url = '/admin/medical/center/' + row.manufacturerId;
            this.$del(url, {}).then((res) => {
              if (JSON.stringify(res.data.code) == 200) {
                // this.tableData = tableData
                this.getCardListData();
                swal("Deleted!", "", "success");
              } else {
                alert(res.data.message)
              }
            })
          } else {
            swal("Cancelled", "", "error");
          }
        });
      },
      detail(data) {
        // console.log('test=='+data.manufacturerId);
        // this.$router.push({path: '/institutionManage', query: {medicalCenterId: data.manufacturerId}});
      },
      //
      getImgCode() {
        this.$nosignget('/api/service/captcha', {
          width: '',
          height: '',
        },).then(res => {
          if (res.status == 200) {
            this.svg = res.data;
            this.isShow = false;
          } else {
            alert(res.statusText);
          }
        }).catch(function (reason) {
          console.log('catch:', reason);
        });
      },
      //
      refreshImgCode() {
        this.getImgCode();
      },
      //
      getMessageCode(graphCode) {
        if (VerifyUtils.validateIsNull(graphCode)) {
          this.$get('/api/service/sms', {
            captcha: graphCode,
            mobile: this.phoneNumber,
          }).then(res => {
            // console.log("test" + JSON.stringify(res));
            if (JSON.stringify(res.data.code) == 200) {
              this.$message({
                message: '',
                type: 'success'
              });
              // console.log("test" + JSON.stringify(res.data.data));
            } else {
              alert(res.data.msg);
            }
          }).catch(function (reason) {
            console.log('catch:', reason);
          });
        }else{
          swal("", "", "error");
        }

      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currentPage = val;
        this.getCardListData();
      },
      unCoopHandleSizeChange(val) {
        console.log(` ${val} `);
      },
      unCoopHandleCurrentChange(val) {
        console.log(`: ${val}`);
        this.unCoopCurrentPage = val;
        this.getCardListData();
      },
      allHandleSizeChange(val) {
        console.log(` ${val} `);
      },
      allHandleCurrentChange(val) {
        console.log(`: ${val}`);
        this.allCurrentPage = val;
        this.getCardListData();
      },
    }
  }
</script>

<style scoped>
  @import "../../styles/personal/personal-center.scss";
</style>



